.liveBox {
    color: white;
    background: #05E100;
    font-size: 12px;
    border-radius: 3px;
    padding: 1px 5px;
    display: inline;
    margin-left: 8px;
  }
  .iconTab2{
    height:5vh;
    width:100vw;
	bottom: 0px;
	display: flex;
	background: #ffffff;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	align-items: center;
	padding: 0 1%;
	z-index: 2000000;
	position: fixed;
}
.chatBox{
    height:83.5%;
    overflow-y:scroll;
    overflow-x: hidden;
}
.textBox{
    background: rgba(86, 103, 255, 0.075);
    border: 0px;
    height:3vh;
    border-radius: 5px;
    padding:3px;
}
.detail{
    bottom:20px;
}
.liveChat {
    height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: Poppins;
  }
.hostModal{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index:300000;
}  
.leaveCard{
    height:20vh;
    width:40vw;
}