.liveChat {
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: Poppins;
}

.setContent{
  text-align: center;
}

.d-none {
  display: none;
}

.navbar {
  display: flex;
  height: 55px;
  padding: 10px 7px;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.goOffLogo {
  margin-right: 0.8%;
  max-width: 70px;
}

.searchBar {
  flex-grow: 0.9;
  display: flex;
  background: rgba(58, 134, 255, 0.08);
  border-radius: 10px;
  padding: 8px 15px;
  margin-top: 5px;
  margin-bottom: 10px;
  justify-content: space-between;
  align-items: center;
}

.leftSearchBarSide {
  display: flex;
}

.searchInput {
  flex-grow: 1;
  font-weight: 500;
  font-size: 16px;
  color: #757D8A;
  border: none;
  background: rgba(58, 134, 255, 0.00);
}

.searchIcon {
  margin-right: 10px;
}

.addPersonIcon, .bellIcon, .shareIcon {
  margin: 8px 0px 13px 1.6%;
  max-width: 37px;
}

.navProfileBox {
  margin-left: 1.6%;
  flex-grow: 0.1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile {
  display: flex;
  background: rgba(58, 134, 255, 0.08);
  border-radius: 20px;
  align-items: center;
  padding: 4px;
}

.profileText {
  color: #757D8A;
  margin-left: 15px;
  margin-right: 15px;
}

.prekshaIcon {
  width: 37px;
  height: 37px;
  border-radius: 100%;
  margin-right: 5%;
}

.mainContent {
  flex-grow: 1;
  display: flex;
}

.leftColumn {
  background: rgba(58, 134, 255, 0.08);
  padding-top: 30px;
  padding-left: 30px;
  padding-right: 30px;
}

.avatarBox, .homeBox, .discoverBox {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  /* padding: 4% 6%;
  margin-top: 7%;
  font-size: 1.25rem; */
  padding: 4% 0;
  border-radius: 10px;
  display: flex;
  align-items: center;
  font-weight: 400;
}

.avatarBox:hover, .homeBox:hover, .discoverBox:hover {
  background: rgba(58, 134, 255, 0.15);
  cursor: pointer;
  transition: background 0.5s;
}

.prekshaIcon, .globeIcon, .homeIcon {
  margin-right: 10px;
}

.avatarName, .globeText, .homeText  {
  color: #757D8A;
  font-weight: 500;
}

.upcommingHeading {
 font-family: Poppins;
 font-style: normal;
 font-weight: 500;
 font-size: 20px;
 line-height: 30px;
 color: #757D8A;
 margin-bottom: 20px;
}

.chatCard {
  flex-grow: 0.3;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  overflow: hidden;
  background-color: white;
  margin-bottom: 26px;
  max-width: 270px;
}

.chatImage {
  flex-grow: 1;
}

.timeStart {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: #01CB01;
  margin-bottom: 3px;
  margin-top: 6px;
}

.chatBottomSide {
  padding-left: 8px;
  padding-right: 8px;
}

.chatTitle {
 margin-top: 0px;
 font-family: Poppins;
 font-style: normal;
 font-weight: 500;
 font-size: 15px;
 line-height: 19px;
 color: #3F3F46;
 margin-bottom: 13px;
}

.middleColumn {
  flex-grow: 1;
}

.rightColumn { 
  display: flex;
  flex-direction: column;
  padding: 18px;
  max-width: 314px;
  background-color: rgba(255, 255, 255, 0.08)
}

.everythingButProfile {
  flex-grow: 1;
}

.chatHeading {
  margin-top: 15px;
  display: flex;
  margin-bottom: 6px;
}

.leftHeading {
  background: rgba(86, 103, 255, 0.15);
  border-radius: 10px;
  padding: 7px;
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  max-height: 48px;
}

.monthText {
  height: 100%;
  color: #3A86FF;
  font-style: normal;
  font-weight: 500;
  text-align: center;
  font-size: 14px;
}

.dayText {
  height: 100%;
  color: #3A86FF;
  font-style: normal;
  font-weight: 500;
  text-align: center;
  line-height: 20px;
  font-size: 25px;
}

.rightHeading {
  display: flex;
  flex-direction: column;
}

.NYTLogo {
  object-fit: contain;
  align-self: flex-start;
}

.articleTitle {
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 21px;
  color: #3F3F46;
}

.startTime {
  display: block;
  font-style: normal;
  font-weight: normal;
  color: #828282;
  font-size: 14px;
  margin-bottom: 5px;
}

.chatTags {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.chatTag {
  text-align: center;
  color: #3A86FF;
  background: rgba(86, 103, 255, 0.15);
  border-radius: 5px;
  padding: 3px 8px;
  font-size: 13px;
  font-weight: bold;
}

.chatDescription {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 18px;
  color: #000000;
  opacity: 0.6;
  margin-bottom: 25px;
}

.dropDownRow {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  display: inline-block;
}

.chatDropDownName {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #3F3F46;
}

.profileBox {
  display: flex;
  justify-content: space-between;
}

.profileLeftSide {
  display: flex;
  margin-right: 8px;
}

.emilyIcon {
  align-self: flex-end;
  object-fit: contain;
  margin-right: 9px;
  width: 3rem;
  height: 3rem;
  border-radius: 100%;
}

.hostText {
  font-weight: 600;
  font-size: 10px;
  text-align: center;
  color: #FFFFFF;
  padding: 1px 2px;
  background: linear-gradient(180deg, #3A86FF 0%, #5667FF 100%);
  border-radius: 2px;
  margin-bottom: 2px;
}

.ProfileNames {
  margin-left: 7px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.profileName {
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  color: #3F3F46;
}

.dots3Icon {
  margin-left: 10px;
}

.middleColumn {
  padding: 15px;
  background-color: rgb(196, 149, 87, 0.07);
}

.innerMiddleBox {
  height: 100%;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.articleHeading {
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-bottom: 5px;
  border-bottom: 1px solid #e0e0e0;
}

.firstRowHeading {
  display: flex;
  justify-content: space-between;
}

.searchForIcon {
  max-height: 15px;
}

.chatTime {
  text-align: center;
  display: block;
  line-height: 25px;
  color: #C4C4C4;
  margin-top: 2px;
  margin-bottom: 23px;
}

.mid-col-articleTitle {
  display: inline;
}

.liveBox {
  margin-left: 11px;
  background: #05E100;
  border-radius: 2px;
  color: white;
  font-size: 13px;
  padding: 1px 4px;
}

.liveChatBox {
  flex-grow: 1;
  padding: 0px 8px 7px 8px;
  overflow: scroll;
  height:82vh;
  overflow-x: hidden;
}

.meMessageBox {
  display: flex;
}

.messageAvatar {
  align-self: flex-end;
  object-fit: contain;
  margin-right: 9px;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 100%;
}

.chatMessageBox {
  background: rgba(58, 134, 255, 0.25);
  border-radius: 20px;
  border-bottom-left-radius: 0px;
  max-width: 500px;
  padding: 4px 12px;
  margin-bottom: 11px;
  margin-top: 2px;
  color: #3A86FF;
}

.messageUserName {
  margin-left: 10px;
  color: #C4C4C4;
  font-size: 14px;
  margin-right: 8px;
}

.otherMessageBox {
  display: flex;
  justify-content: flex-end;
}

.otherMessageBox .messageAvatar {
  margin-right: 0px;
  margin-left: 9px;
}

.otherMessageBox .chatMessageBox {
  background: rgba(86, 103, 255, 0.25);
  border-radius: 20px;
  border-bottom-right-radius: 0px;
  color: #5667FF;
}

.chatInputBox {
  background: linear-gradient(180deg, rgba(58, 134, 255, 0.075) 0%, rgba(86, 103, 255, 0.075) 100%);
  display: flex;
  padding: 8px 12px;
  -ms-flex-align: top;
  max-height: 45px;
  width: 100%;
  border-radius: 12px;
}

.formbox {
  flex-grow: 10;
}

.inputText {
  /* position: absolute; */
  border-radius: 20px;
  max-height: 45px;
  height: 35px;
  padding-left: 5px;
  color: grey;
  background: transparent;
  border: none;
  padding-bottom: 5px;
  width: 100%;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 15px;
}
button{
  border: none;
  color: darkgray;
  padding: 12px 22px;
  text-align: center;
  border-radius: 15px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

button:hover{
  color:  grey;
}
.inputAddIcon {
  height: 100%;
  max-height: 50px;
}

.inputSendIcon {
  max-height: 50px;
  cursor: pointer;
}

.inputText:focus, .inputText:focus, .inputText:focus,
.searchInput:focus, .searchInput:focus, .searchInput:focus {
  outline: none;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #F9F9F9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}
.dropDownRow:hover .dropdown-content {
  display: block;
}